@import url(https://fonts.googleapis.com/css?family=Inconsolata);
.wrapper-loading-custom{background:#0b0b14;font-family:"Inconsolata", monospace;overflow:hidden;width:100%;height:100%;z-index:99999;position:fixed;opacity:0.7}.wrapper-loading-custom h1{position:absolute;height:40px;margin:auto;top:200px;left:0;right:0;bottom:0;text-transform:uppercase;text-align:center;letter-spacing:0.1em;font-size:14px;font-weight:lighter;color:white}.wrapper-loading-custom h1 span{display:none}.wrapper-loading-custom h1::after{animation:txt 5s infinite;content:""}.wrapper-loading-custom .arc{z-index:999;position:absolute;margin:auto;top:0;right:0;bottom:0;left:0;width:100px;height:100px;border-radius:50%;border-top:2px solid #ffea29;border-left:1px solid transparent;border-right:1px solid transparent;animation:rt 2s infinite linear}.wrapper-loading-custom .arc::before{position:absolute;margin:auto;top:0;right:0;bottom:0;left:0;width:70px;height:70px;border-radius:50%;border-top:2px solid #8d29ff;border-left:1px solid transparent;border-right:1px solid transparent;animation:rt 4s infinite linear reverse;content:""}.wrapper-loading-custom .arc::after{position:absolute;margin:auto;top:0;right:0;bottom:0;left:0;width:0;height:0;border-radius:50%;border-top:0;border-top:initial;border-left:0;border-left:initial;border-right:0;border-right:initial;animation:cw 1s infinite;content:"";background:snow}@keyframes rt{100%{transform:rotate(360deg)}}@keyframes cw{0%{width:0;height:0}75%{width:40px;height:40px}100%{width:0;height:0}}@keyframes txt{0%{content:"LOADING."}50%{content:"LOADING.."}100%{content:"LOADING..."}}

.wrapper-footer{padding:0 5px 15px;background-color:#141414;color:#ccc}.wrapper-footer .copyright{width:100%;text-align:center}.wrapper-footer .copyright span{color:#f83253;font-size:13px}.wrapper-footer .footer-container{padding:0 40px}.wrapper-footer .footer-grid{padding-top:70px}.wrapper-footer .footer-grid .logo{margin-top:-8px;margin-bottom:46px}.wrapper-footer a{text-decoration:none;color:#c5c3c3}.wrapper-footer ul{padding:0;margin:0}.wrapper-footer .contact{margin:30px 0}.wrapper-footer .contact p{font-size:16px;font-weight:400;line-height:26px}.wrapper-footer .follow{display:flex;flex-wrap:wrap}.wrapper-footer .follow h3{font-size:12px;font-weight:700;margin-top:10px;margin-bottom:20px;letter-spacing:2px;text-transform:uppercase;width:100%}.wrapper-footer .follow a{display:inline-block;position:relative;width:46px;height:45px;background:#fff;line-height:46px;border-radius:100%;transition:all 0.3s ease 0s;text-align:center;color:black;margin:0 8px;font-size:16px;z-index:1}.wrapper-footer .follow a::before{content:'';position:absolute;top:-1px;left:-1px;right:-1px;bottom:-1px;background-color:#24baef;opacity:0;visibility:hidden;transform:scale(0.3);border-radius:50%;z-index:-1}.wrapper-footer .follow a:hover::before{opacity:1;visibility:visible;transform:scale(1);transition:0.7s}.wrapper-footer .follow a:hover{color:white;transition:0.7s}.wrapper-footer .footer-grid h3{font-size:14px;font-weight:700;text-transform:uppercase;letter-spacing:3px;padding-bottom:16px;margin-bottom:34px;position:relative}.wrapper-footer .footer-grid h3::before{position:absolute;width:48px;height:1px;background:#fff;content:"";bottom:0}.wrapper-footer .footer-grid ul li a{font-size:16px;font-weight:400;line-height:16px;margin-bottom:16px;display:block;transition:.3s;position:relative}.wrapper-footer .footer-grid ul li a:hover{color:#24baef !important;padding-left:30px}.wrapper-footer .footer-grid ul li a::before{position:absolute;content:"";width:0;height:1px;background:#24baef;transition:.3s;left:0;top:calc(50% - .5px)}.wrapper-footer .footer-grid ul li a:hover::before{width:20px}.wrapper-footer .payment_methods{margin-bottom:30px}.wrapper-footer .payment_methods img{width:100%;width:50px}@media only screen and (max-width: 600px){.footer-grid{padding-top:40px}}@media only screen and (max-width: 411px){.wrapper-footer .footer-container{padding:0px 17px;justify-content:space-between}}

@keyframes animatedBackground{0%{background-position:0 0}100%{background-position:0 -300px}}@keyframes opacityBg{0%{opacity:0}50%{opacity:1}100%{}}@keyframes changeBg{0%{background:#141414;opacity:0.3}100%{background:#141414;opacity:1}}.wrapper-header{flex-grow:1}.wrapper-header .bg-trans{background:transparent;box-shadow:none;transition:all 0.5s ease;background-position:center;background-repeat:no-repeat;background-size:cover}.wrapper-header .bg-header{width:100%;background-position:0px 0px;animation-name:changeBg;animation-duration:0.5s;animation-iteration-count:1;animation-fill-mode:forwards;box-shadow:0px 2px 4px -1px rgba(0,0,0,0.2),0px 4px 5px 0px rgba(0,0,0,0.14),0px 1px 10px 0px rgba(0,0,0,0.12)}

html{scroll-behavior:smooth}.justify-content-center{justify-content:center !important}.text-align-center{text-align:center !important}.mt-8{margin-top:8px}.w-100{width:100% !important}a{text-decoration:none !important}ul{margin:0 !important;padding:0 !important}li{list-style:none !important}

